
* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
body {
  background-color: #f2f2f2;
  color: #333;
  font-family: Arial, sans-serif;
}
.center {
  margin: auto;
  min-width: 360px;
}
@media screen and (min-width: 640px) {
.center {
      width: 576px;
}
}
@media screen and (min-width: 720px) {
.center {
      width: 640px;
}
}
@media screen and (min-width: 800px) {
.center {
      width: 720px;
}
}
@media screen and (min-width: 1024px) {
.center {
      width: 960px;
}
}
@media screen and (min-width: 1200px) {
.center {
      width: 1080px;
}
}
.text-center {
  text-align: center;
}
@media screen and (max-width: 639px) {
.xs-padding {
    padding: 0 1rem;
}
}
p {
  text-align: justify;
}
a {
  color: #0092f0;
  text-decoration: none;
}
a:hover {
    color: #0b9fff;
}
.error, .loading {
  width: 100%;
  margin-top: 1rem;
  border-radius: 1rem;
  padding: 1rem;
  font-size: 1.25rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 150px;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.error {
  background-color: rgba(182, 0, 0, 0.5);
  color: #f2f2f2;
  border: 1px solid #d00000;
}
.error .fa {
    margin-right: .5rem;
}
#app > header {
  padding-top: 1rem;
  overflow: auto;
  background-image: url("/static/img/background.jpg");
  background-size: cover;
  color: #f2f2f2;
  -webkit-box-shadow: 0 0 2px 2px #777;
          box-shadow: 0 0 2px 2px #777;
}
#app > header h1 {
    margin: 0;
    text-align: right;
    line-height: 128px;
}
#app > header .brand {
    margin-bottom: 1rem;
}
#app > header nav {
    background-color: #0092f0;
}
#app > header ul {
    margin: 0;
    padding: 0;
    list-style-type: none;
    overflow: auto;
}
#app > header ul li {
      float: left;
}
#app > header ul a {
      display: block;
      line-height: 50px;
      color: #f2f2f2;
      text-decoration: none;
      padding: 0 1rem;
}
#app > header ul a:hover {
        background-color: #0b9fff;
}
#avatar {
  border-radius: 100%;
  height: 128px;
  float: left;
  border: 8px solid #fff;
}



